import { fabric } from 'fabric';

let sign =
  Math.sign ||
  function (x) {
    return (x > 0) - (x < 0) || +x;
  };

const getLocalPoint = (transform, originX, originY, x, y) => {
  let target = transform.target;
  let control = target.controls[transform.corner];
  let zoom = target.canvas.getZoom();
  let padding = target.padding / zoom;
  let localPoint = target.toLocalPoint(
    new fabric.Point(x, y),
    originX,
    originY
  );
  if (localPoint.x >= padding) localPoint.x -= padding;
  if (localPoint.x <= -padding) localPoint.x += padding;
  if (localPoint.y >= padding) localPoint.y -= padding;
  if (localPoint.y <= padding) localPoint.y += padding;
  localPoint.x -= control.offsetX;
  localPoint.y -= control.offsetY;
  return localPoint;
};

const handleXCrop = (
  target,
  newWidth,
  distance,
  isLeft,
  shouldSetScaleCrop
) => {
  if (!target.cropRect) {
    target.cropRect = {
      h: target.height,
      w: 0,
      x: 0,
      y: 0,
    };
  }
  target.width = newWidth;
  target.cropRect.w = newWidth;
  if (isLeft) target.cropRect.x = target.originalWidth - distance;
  if (shouldSetScaleCrop) {
    target.scaleCrop = { ...target.cropRect, s: target.scaleX };
  }
};

const handleXScale = (target, newWidth, distance, isLeft) => {
  let cropRect = target.cropRect;
  let oldHeight = target.height * target.scaleX;
  let oldCropHeight = cropRect ? cropRect.h : target.originalHeight;
  let scale = target.scaleX * (newWidth / target.width);
  let newHeight = oldHeight / scale;
  let newY = target.cropRect.y + (oldCropHeight - newHeight) / 2;

  if (newHeight > target.scaleCrop.h) {
    scale = target.scaleCrop.s;
    newHeight = target.scaleCrop.h;
    newY = target.scaleCrop.y;
    target.scaleCrop = null;
  }
  if (!cropRect) target.cropRect = { w: target.width, h: 0, x: 0, y: 0 };
  target.scaleX = scale;
  target.scaleY = scale;
  target.height = newHeight;
  target.cropRect.h = newHeight;
  target.cropRect.y = newY;
  if (!target.scaleCrop) handleXCrop(target, newWidth, distance, isLeft, false);
};

const handleXScaleCrop = (target, newPoint) => {
  let isLeft = newPoint.x < 0;
  let newWidth = Math.abs(newPoint.x / target.scaleX);
  let cropRect = target.cropRect;
  let leftover = 0;
  if (!isLeft) leftover = cropRect ? cropRect.x : 0;
  else {
    leftover = cropRect ? target.originalWidth - (cropRect.x + cropRect.w) : 0;
  }
  let distance = newWidth + leftover;
  if (!target.scaleCrop && target.originalWidth > distance) {
    handleXCrop(target, newWidth, distance, isLeft, false);
  } else {
    if (!target.scaleCrop) {
      let maxWidth = target.originalWidth - leftover;
      distance = maxWidth + leftover;
      handleXCrop(target, maxWidth, distance, isLeft, true);
    }
    handleXScale(target, newWidth, distance, isLeft);
  }
};

const handleYCrop = (
  target,
  newHeight,
  distance,
  isTop,
  shouldSetScaleCrop
) => {
  if (!target.cropRect) {
    target.cropRect = {
      w: target.width,
      h: 0,
      x: 0,
      y: 0,
    };
  }
  target.height = newHeight;
  target.cropRect.h = newHeight;
  if (isTop) target.cropRect.y = target.originalHeight - distance;
  if (shouldSetScaleCrop) {
    target.scaleCrop = { ...target.cropRect, s: target.scaleX };
  }
};

const handleYScale = (target, newHeight, distance, isTop) => {
  let cropRect = target.cropRect;
  let oldWidth = target.width * target.scaleX;
  let oldCropWidth = cropRect ? cropRect.w : target.originalWidth;
  let scale = target.scaleX * (newHeight / target.height);
  let newWidth = oldWidth / scale;
  let newX = target.cropRect.x + (oldCropWidth - newWidth) / 2;

  if (newWidth > target.scaleCrop.w) {
    scale = target.scaleCrop.s;
    newWidth = target.scaleCrop.w;
    newX = target.scaleCrop.x;
    target.scaleCrop = null;
  }
  if (!cropRect) target.cropRect = { h: target.height, w: 0, x: 0, y: 0 };
  target.scaleX = scale;
  target.scaleY = scale;
  target.width = newWidth;
  target.cropRect.w = newWidth;
  target.cropRect.x = newX;
  if (!target.scaleCrop) handleYCrop(target, newHeight, distance, isTop, false);
};

const handleYScaleCrop = (target, newPoint) => {
  let isTop = newPoint.y < 0;
  let newHeight = Math.abs(newPoint.y / target.scaleX);
  let cropRect = target.cropRect;
  let leftover = 0;
  if (!isTop) leftover = cropRect ? cropRect.y : 0;
  else {
    leftover = cropRect ? target.originalHeight - (cropRect.y + cropRect.h) : 0;
  }
  let distance = newHeight + leftover;
  if (!target.scaleCrop && target.originalHeight > distance) {
    handleYCrop(target, newHeight, distance, isTop, false);
  } else {
    if (!target.scaleCrop) {
      let maxHeight = target.originalHeight - leftover;
      distance = maxHeight + leftover;
      handleYCrop(target, maxHeight, distance, isTop, true);
    }
    handleYScale(target, newHeight, distance, isTop);
  }
};

const scaleObject = (eventData, transform, x, y, options) => {
  options = options || {};
  let target = transform.target;
  let by = options.by;
  let newPoint = getLocalPoint(
    transform,
    transform.originX,
    transform.originY,
    x,
    y
  );

  let signX = by !== 'y' ? sign(newPoint.x) : 1;
  let signY = by !== 'x' ? sign(newPoint.y) : 1;
  if (!transform.signX) transform.signX = signX;
  if (!transform.signY) transform.signY = signY;

  if (
    target.lockScalingFlip &&
    (transform.signX !== signX || transform.signY !== signY)
  ) {
    return false;
  }

  let oldW = target.cropRect ? target.cropRect.w : null;
  let oldH = target.cropRect ? target.cropRect.h : null;
  let oldScale = target.scaleX;

  if (by && by === 'x') handleXScaleCrop(target, newPoint);
  else if (by && by === 'y') handleYScaleCrop(target, newPoint);
  let scaleChanged = oldScale !== target.scaleX;
  let sizeChanged =
    target.cropRect !== null &&
    (oldW !== target.cropRect.w || oldH !== target.cropRect.h);
  let didChange = scaleChanged || sizeChanged;
  if (didChange && target.clipPath) target.clipPath = null;
  return didChange;
};

const fireEvent = fabric.controlsUtils.fireEvent;
const wrapWithFixedAnchor = fabric.controlsUtils.wrapWithFixedAnchor;

const scaleObjectX = (eventData, transform, x, y) => {
  return scaleObject(eventData, transform, x, y, { by: 'x' });
};
const scaleObjectY = (eventData, transform, x, y) => {
  return scaleObject(eventData, transform, x, y, { by: 'y' });
};

const commonEventInfo = (eventData, transform, x, y) => {
  return {
    e: eventData,
    transform: transform,
    pointer: {
      x: x,
      y: y,
    },
  };
};

const wrapWithFireEvent = (eventName, actionHandler) => {
  return (eventData, transform, x, y) => {
    let actionPerformed = actionHandler(eventData, transform, x, y);
    if (actionPerformed) {
      fireEvent(eventName, commonEventInfo(eventData, transform, x, y));
    }
    return actionPerformed;
  };
};

export const scalingX = wrapWithFireEvent(
  'scaling',
  wrapWithFixedAnchor(scaleObjectX)
);
export const scalingY = wrapWithFireEvent(
  'scaling',
  wrapWithFixedAnchor(scaleObjectY)
);
