import { useCallback, useEffect, useState } from 'react';
import { handlePaste } from '../../../utils/pasteObject';
import { keyDownHandler, keyUpHandler } from '../utils/keyEvents';

const useKeyEvents = (
  canvas,
  clipboard,
  setClipboard,
  objId,
  updateId,
  fabricSub,
  dispatch,
  setShouldCut,
  state,
  videoRef,
  isPlaying,
  setIsPrevWord,
  setIsNextWord
) => {
  const [isPasting, setIsPasting] = useState(false);
  const [isCopying, setIsCopying] = useState(false);
  const [isCutting, setIsCutting] = useState(false);
  const [isBringToFront, setIsBringToFront] = useState(false);
  const [isSendToBack, setIsSendToBack] = useState(false);
  const [isCut, setIsCut] = useState(false);
  const [isPlayPause, setIsPlayPause] = useState(false);
  const [shouldRun, setShouldRun] = useState(true);

  const handleEvents = useCallback(() => {
    if (!state.isLoadingTemplate && !state.isCropMode) {
      if (canvas && isPasting && clipboard) {
        setShouldRun(false);
        handlePaste(
          canvas,
          clipboard,
          objId,
          updateId,
          setClipboard,
          null,
          fabricSub
        );
      } else if (canvas && (isCopying || isCutting)) {
        setShouldRun(false);
        let active = canvas.getActiveObject();
        if (active && active.type !== 'video' && active.id !== 'subtitle') {
          setClipboard({
            object: active,
            coords: { top: active.top, left: active.left },
          });
          if (isCutting) canvas.remove(active);
        }
      } else if (canvas && isBringToFront) {
        setShouldRun(false);
        let active = canvas.getActiveObject();
        if (active && active.id !== 'subtitle') {
          canvas.bringToFront(active);
          if (fabricSub) canvas.bringToFront(fabricSub);
          dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
        }
      } else if (canvas && isSendToBack) {
        setShouldRun(false);
        let active = canvas.getActiveObject();
        if (active && active.id !== 'subtitle') {
          canvas.sendToBack(active);
          dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
        }
      } else if (canvas && isPlayPause) {
        setShouldRun(false);
        if (isPlaying) videoRef.current.pause();
        else videoRef.current.play();
      } else if (isCut) {
        setShouldRun(false);
        setShouldCut(true);
      }
    }
  }, [
    canvas,
    isPasting,
    isCopying,
    isBringToFront,
    isSendToBack,
    isCut,
    isPlayPause,
    objId,
    setClipboard,
    clipboard,
    updateId,
    fabricSub,
    dispatch,
    setShouldCut,
    state.isLoadingTemplate,
    state.isCropMode,
    videoRef,
    isPlaying,
    isCutting,
  ]);

  useEffect(() => {
    if (shouldRun) handleEvents();
  }, [
    shouldRun,
    handleEvents,
    isPasting,
    isCopying,
    isCutting,
    isBringToFront,
    isSendToBack,
    isCut,
    isPlayPause,
  ]);

  const onKeyDown = useCallback(
    (e) => {
      keyDownHandler(
        e,
        canvas,
        setIsPasting,
        setIsCopying,
        setIsCutting,
        setIsBringToFront,
        setIsSendToBack,
        dispatch,
        setIsCut,
        setIsPlayPause,
        setIsPrevWord,
        setIsNextWord
      );
    },
    [canvas, dispatch, setIsPrevWord, setIsNextWord]
  );

  const onKeyUp = useCallback(
    (e) => {
      keyUpHandler(
        setIsPasting,
        setIsCopying,
        setIsCutting,
        setIsBringToFront,
        setIsSendToBack,
        setIsCut,
        setShouldRun,
        setIsPlayPause,
        setIsPrevWord,
        setIsNextWord
      );
    },
    [setIsPrevWord, setIsNextWord]
  );

  useEffect(() => {
    document.addEventListener('keydown', onKeyDown);
    document.addEventListener('keyup', onKeyUp);

    return () => {
      document.removeEventListener('keydown', onKeyDown);
      document.removeEventListener('keyup', onKeyUp);
    };
  }, [onKeyDown, onKeyUp]);
};

export default useKeyEvents;
