import React, { useState, useEffect, useCallback } from 'react';
import styles from './Transcription.module.css';
import cut from '../../assets/editor/cut.svg';
import search from '../../assets/editor/search.svg';
import {
  deleteWord,
  restoreDeleted,
  deleteSelection,
  findAndSetCurrentWord,
} from './utils/updateTextAndCuts';
import Words from './Words/Words';
import {
  findWordIndexWithId,
  findPrevNotDeletedWord,
  findNextNotDeletedWord,
} from '../../utils/findIndex';
import { handleSelection } from './utils/selection';
import TranscriptionProgress from './TranscriptionProgress/TranscriptionProgress';

const Transcription = ({
  words,
  currentWordIndex,
  setCurrentWordIndex,
  isPlaying,
  videoRef,
  setCuts,
  currentSelection,
  setCurrentSelection,
  currentSub,
  fabricSub,
  setShouldRerenderSub,
  setCurrentTime,
  setNextCutIndex,
  videoCuts,
  status,
  shouldCut,
  setShouldCut,
  isNextWord,
  setIsNextWord,
  isPrevWord,
  setIsPrevWord,
}) => {
  const [searchInput, setSearchInput] = useState('');
  const [inputIndex, setInputIndex] = useState(null);
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [isFinished, setIsFinished] = useState(false);

  const setPlayerTime = useCallback(
    (wordIndex, cuts = videoCuts) => {
      let endTime = '0';
      if (wordIndex !== null) {
        let word = words[wordIndex];
        endTime = word.end;
        videoRef.current.currentTime = +word.start;
        setCurrentTime(+word.start);
        // console.log('seting player time: ', +word.start);
      }
      let cutIndex = 0;
      for (let i = 0; i < cuts.length; i++) {
        let cut = cuts[i];
        if (+endTime > +cut.end) {
          cutIndex++;
        }
      }
      setNextCutIndex(cutIndex);
    },
    [setCurrentTime, setNextCutIndex, videoCuts, videoRef, words]
  );

  const cutHandler = useCallback(() => {
    if (words) {
      if (isPlaying) videoRef.current.pause();
      if (!currentSelection && currentWordIndex !== null && words) {
        deleteWord(
          words,
          currentWordIndex,
          setCurrentWordIndex,
          setCuts,
          setPlayerTime
        );
      } else if (currentSelection && words) {
        deleteSelection(
          words,
          setCurrentWordIndex,
          currentSelection,
          setCurrentSelection,
          setCuts,
          setPlayerTime
        );
      }
      let selection = document.getSelection();
      selection.removeAllRanges();
    }
  }, [
    currentSelection,
    currentWordIndex,
    isPlaying,
    setCurrentSelection,
    setCurrentWordIndex,
    setCuts,
    setPlayerTime,
    videoRef,
    words,
  ]);

  const handleNexOrPrevWord = useCallback((setState, callback) => {
    setState(false);
    videoRef.current.pause();
    let index = callback(currentWordIndex, words);
    if (index !== null) {
      let selection = document.getSelection();
      selection.removeAllRanges();
      setCurrentSelection(null);
      setPlayerTime(index);
      setCurrentWordIndex(index);
    }
  }, [
    currentWordIndex,
    setCurrentWordIndex,
    words,
    videoRef,
    setPlayerTime,
    setCurrentSelection,
  ]);

  useEffect(() => {
    if (shouldCut) {
      setShouldCut(false);
      cutHandler();
    } else if (isNextWord && words) {
      handleNexOrPrevWord(setIsNextWord, findNextNotDeletedWord);
    } else if (isPrevWord && words) {
      handleNexOrPrevWord(setIsPrevWord, findPrevNotDeletedWord);
    }
  }, [
    shouldCut,
    setShouldCut,
    cutHandler,
    isNextWord,
    setIsNextWord,
    isPrevWord,
    setIsPrevWord,
    words,
    handleNexOrPrevWord
  ]);

  useEffect(() => {
    if (isFirstLoad && words) {
      setIsFirstLoad(null);
      findAndSetCurrentWord(
        0,
        words,
        setCurrentWordIndex,
        setCuts,
        setPlayerTime
      );
    }
  }, [isFirstLoad, words, setCuts, setPlayerTime, setCurrentWordIndex]);

  const wordClickHandler = (e, isDeleted) => {
    let id = e.currentTarget.id;
    let wordIndex = findWordIndexWithId(id, words);
    if (isPlaying) videoRef.current.pause();
    if (e.detail > 1 && inputIndex === null) setInputIndex(wordIndex);
    if (!isDeleted && inputIndex === null && wordIndex !== null) {
      setCurrentSelection(null);
      setPlayerTime(wordIndex);
      setCurrentWordIndex(wordIndex);
    } else if (inputIndex === null) {
      restoreDeleted(
        words,
        wordIndex,
        setCurrentWordIndex,
        setCurrentSelection,
        setCuts,
        setPlayerTime
      );
    }
  };

  const mouseUpHandler = useCallback(
    (e) => {
      let selection = document.getSelection();
      if (e.detail < 2 && selection.anchorNode && selection.focusNode) {
        let anchorNode = selection.anchorNode.parentNode;
        let focusNode = selection.focusNode.parentNode;
        let isAnchorNode = anchorNode.tagName === 'SPAN';
        let isFocusNode = focusNode.tagName === 'SPAN';
        let position = selection.anchorNode.compareDocumentPosition(
          selection.focusNode
        );
        if ((isAnchorNode || isFocusNode) && !selection.isCollapsed) {
          if (isPlaying) videoRef.current.pause();
          handleSelection(
            selection,
            anchorNode,
            focusNode,
            isAnchorNode,
            isFocusNode,
            position,
            setCurrentWordIndex,
            setCurrentSelection,
            setPlayerTime,
            words
          );
        }
      }
    },
    [
      isPlaying,
      videoRef,
      setCurrentWordIndex,
      setCurrentSelection,
      setPlayerTime,
      words,
    ]
  );

  useEffect(() => {
    document.addEventListener('mouseup', mouseUpHandler);
    return () => document.removeEventListener('mouseup', mouseUpHandler);
  }, [mouseUpHandler]);

  return (
    <div className={styles.Transcription}>
      <div className={styles.TextControls}>
        <div className={styles.CutContainer} onClick={cutHandler}>
          <img src={cut} alt='cut' />
          <p>Cut</p>
        </div>
        <div className={styles.SearchContainer}>
          <input
            value={searchInput}
            onChange={(e) => setSearchInput(e.target.value)}
            className={styles.SearchInput}
            placeholder='Search'
            type='text'
          />
          <img className={styles.SearchSvg} src={search} alt='search' />
        </div>
      </div>
      <div id='textbox' className={styles.Text}>
        {isFinished ? (
          <Words
            wordsArr={words}
            currentWordIndex={currentWordIndex}
            searchInput={searchInput}
            wordClickHandler={wordClickHandler}
            inputIndex={inputIndex}
            setInputIndex={setInputIndex}
            currentSelection={currentSelection}
            fabricSub={fabricSub}
            currentSub={currentSub}
            setShouldRerenderSub={setShouldRerenderSub}
          />
        ) : (
          <TranscriptionProgress
            status={status}
            words={words}
            setIsFinished={setIsFinished}
          />
        )}
      </div>
    </div>
  );
};

export default Transcription;
